<template>
  <div class="main">
    <h1 class="title">BeaverBoard</h1>
    <div class="sumb">
      <div v-for="a in mainData[selected - 1]" :key="a" class="subm" :style="{ 'backgroundColor': groups[a.group][1] }">
        {{ groups[a.group][0] }}: {{ a.steps }}

      </div>
    </div>
    <div class="board">

      <div @click="setSelcted(index)" :class="{ level: index != selected, sLevel: index == selected, levelG: true }"
        :style="{ 'backgroundColor': winners[index - 1].length > 0 ? groups[winners[index - 1]][1] : '', 'borderColor': winners[index - 1].length > 0 ? groups[winners[index - 1]][1] : '' }"
        v-for="index in 16" :key="index">
        <div class="length">{{ index }}</div>
        <div class="notifications">
          <BootstrapIcon :style="{'backgroundColor': groups[mostRecentTeam][1]}" v-if="mostRecent == index"
            class="mostRecent" icon="stopwatch-fill" size="2x" />
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import getData from "@/db.js"
export default {
  name: 'App',
  components: {

  }, data: () => {
    return {
      mainData: [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], []],
      winners: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
      groups: {},
      selected: 1,
      mostRecent: -1,
      mostRecentTeam: "",
      dateN: new Date(0),
    }
  }, methods: {
    setSelcted: function (index) {
      this.selected = index
    }
  }, computed: {

  },
  created: function () {
    // console.log(getRecords())
    getData().then((done) => {
      
      // Setup Groups
      let fin = {}
      let groupStuff = {}
      done.groups.forEach((group) => {
        fin[group._id] = [group.name, group.color]
        groupStuff[group._id] = []

      })
      this.groups = fin;
      

      let attempts = [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], []]
      let attemptGroups =  [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], []];
      
      // Only keep top

      for (let i = 0; i < attempts.length; i++) {
        for (let j = 0; j < done.attempts[i].length; j++) {
          if (!(attemptGroups[i].includes(done.attempts[i][j].group))) {
            attempts[i].push(done.attempts[i][j])
            attemptGroups[i].push(done.attempts[i][j].group)
          }

        }
      }
      console.log(attemptGroups)
      this.mainData = attempts;

      

      // Object.keys(attempts).forEach((group) => {
      //   attempts[group].sort((a,b) => -a.steps + b.steps)
      // })




      // Get Most Recent
      this.dateN = new Date(0);
      done.attempts.forEach((length) => {
        length.forEach((attempt) => {
          if (new Date(attempt.createdAt) > this.dateN) {
            this.mostRecent = attempt.length
            this.mostRecentTeam = attempt.group
            this.dateN = new Date(attempt.createdAt);
          }
        })
      })



      // Find Winner
      this.mainData.forEach((length, index) => {


        if (length.length > 1) {
          console.log(length)
          if (length[0].steps != length[1].steps) {
            this.winners[index] = length[0].group
          }

        } else if (length.length == 1) {
          this.winners[index] = length[0].group
        }


      })

      console.log(this.winners)


    })
  }
}
</script>

